.footer {
  background: conic-gradient(from 0deg at center, #fff, #000);
  color: #fff;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer-content {
  width: 100%;
  max-width: 1100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-left p {
  margin: 0;
}

.footer-right {
  display: flex;
  justify-content: space-between;
}

.footer-right a {
  color: #fff;
  margin-right: 10px;
}

.footer-right a:last-child {
  margin-right: 10px;
}

